
import React from 'react';

// 
function PromptManager({
  editingPrompts,
  setEditingPrompts,
  editingChanged,
  setEditingChanged,
  saveCustomPrompts,
  setShowPromptManager
}) {
  //  editingPrompts 
  const safeEditingPrompts = Array.isArray(editingPrompts) ? editingPrompts.filter(x => x && typeof x === 'object') : [];
  return (
    <div style={{ position: 'fixed', top: 0, left: 0, width: '100vw', height: '100vh', background: 'rgba(0,0,0,0.18)', zIndex: 3000, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
      <div style={{ background: '#fff', borderRadius: 16, boxShadow: '0 4px 32px #2563eb22', padding: 32, minWidth: 420, maxWidth: 540, width: '100%', position: 'relative', display: 'flex', flexDirection: 'column', maxHeight: '90vh', overflow: 'auto' }}>
        <button style={{ position: 'absolute', top: 12, right: 18, background: 'none', border: 'none', color: '#2563eb', fontSize: 22, fontWeight: 700, cursor: 'pointer' }} onClick={()=>setShowPromptManager(false)} title=""></button>
        <h3 style={{marginTop:0,marginBottom:18,fontWeight:700,fontSize:20,color:'#2563eb'}}>AI</h3>
        <div style={{marginBottom:18}}>
          {safeEditingPrompts.map((tpl, idx) => (
            <div key={idx} style={{marginBottom:10,padding:10,border:'1px solid #b6c2d1',borderRadius:8,background:'#f8fafc'}}>
              <input type="text" value={tpl.label} onChange={e => {
                const arr = [...safeEditingPrompts];
                arr[idx].label = e.target.value;
                setEditingPrompts(arr);
                setEditingChanged(true);
              }} style={{fontWeight:600,fontSize:15,padding:'4px 12px',borderRadius:6,border:'1.2px solid #b6c2d1',width:'60%',marginRight:8}} />
              <button onClick={()=>{
                const arr = safeEditingPrompts.filter((_,i)=>i!==idx);
                setEditingPrompts(arr);
                setEditingChanged(true);
              }} style={{color:'#f87171',background:'none',border:'none',fontWeight:700,fontSize:16,cursor:'pointer'}}></button>
              <textarea value={tpl.value} onChange={e => {
                const arr = [...safeEditingPrompts];
                arr[idx].value = e.target.value;
                setEditingPrompts(arr);
                setEditingChanged(true);
              }} rows={3} style={{width:'100%',border:'1px solid #b6c2d1',borderRadius:6,padding:8,fontSize:14,marginTop:6,resize:'vertical'}} />
            </div>
          ))}
          <button onClick={()=>{
            const arr = [...safeEditingPrompts,{label:'',value:''}];
            setEditingPrompts(arr);
            setEditingChanged(true);
          }} style={{background:'#2563eb',color:'#fff',border:'none',borderRadius:8,padding:'6px 18px',fontWeight:700,fontSize:15,cursor:'pointer',marginTop:8}}></button>
        </div>
        <div style={{textAlign:'right',display:'flex',gap:12,justifyContent:'flex-end'}}>
          <button onClick={()=>setShowPromptManager(false)} style={{background:'#f3f6fa',color:'#2563eb',border:'1px solid #2563eb',borderRadius:8,padding:'6px 18px',fontWeight:700,fontSize:15,cursor:'pointer'}}></button>
          <button disabled={!editingChanged} onClick={()=>saveCustomPrompts(safeEditingPrompts)} style={{background:editingChanged?'#2563eb':'#b6c2d1',color:'#fff',border:'none',borderRadius:8,padding:'6px 18px',fontWeight:700,fontSize:15,cursor:editingChanged?'pointer':'not-allowed'}}></button>
        </div>
      </div>
    </div>
  );
}

export default PromptManager;
