/**
 * /electron
 *  [{ name, path, isDirectory, children }]
 * @param {string} directory 
 */
async function readLocalDir(directory) {
  if (!window.electronAPI || !directory) return [];
  try {
    const nodes = await window.electronAPI.readDir(directory);
    // nodes: [{ name, path, isDirectory, children }]
    //  children
    const processNodes = (arr) =>
      arr.map((item) => {
        if (item.isDirectory && item.children) {
          return {
            ...item,
            children: processNodes(item.children),
            type: 'folder',
          };
        } else {
          return {
            ...item,
            type: 'file',
            fileObj: item, //  handleOpenFile
          };
        }
      });
    return processNodes(nodes);
  } catch (e) {
    console.error('', e);
    return [];
  }
}
// 
const buildFolderTree = (files) => {
  const root = { name: 'root', type: 'folder', children: [] };
  for (const file of files) {
    const relPath = file.__relativePath || file.webkitRelativePath.replace(/^[^/]*\//, '');
    const parts = relPath.split('/');
    let current = root;
    for (let i = 0; i < parts.length; i++) {
      const part = parts[i];
      if (i === parts.length - 1) {
        // 
        current.children.push({
          name: part,
          type: 'file',
          path: '/' + relPath.replace(/\\/g, '/'),
          fileObj: file,
        });
      } else {
        let folder = current.children.find((c) => c.type === 'folder' && c.name === part);
        if (!folder) {
          folder = { name: part, type: 'folder', children: [] };
          current.children.push(folder);
        }
        current = folder;
      }
    }
  }
  return root.children;
};

