import React from 'react';

function MarkdownRawPreview({ content, titlePrefix, paraPrefix, mode, theoremPatternStr, numberPatternStr, definitionsPatternStr, examplesPatternStr }) {
  const containerRef = React.useRef(null);
  const [currentIdx, setCurrentIdx] = React.useState(0);
  const [jumpType, setJumpType] = React.useState('all'); // all/title/para
  const [useRegex, setUseRegex] = React.useState(true); // 
  const [theoremViewType, setTheoremViewType] = React.useState('all'); // all/theorem/number/definition/example
  const [theoremCurrentIdx, setTheoremCurrentIdx] = React.useState(0); // 

  // 
  const { highlightLines, titleIdxs, paraIdxs, allIdxs } = React.useMemo(() => {
    const lines = content ? content.split(/\r?\n/) : [];
    if (mode === 'theorem') {
      let theoremReg, numberReg, defReg, exReg;
      try {
        theoremReg = theoremPatternStr ? new RegExp(theoremPatternStr, 'i') : null;
        numberReg = numberPatternStr ? new RegExp(numberPatternStr, 'i') : null;
        defReg = definitionsPatternStr ? new RegExp(definitionsPatternStr, 'i') : null;
        exReg = examplesPatternStr ? new RegExp(examplesPatternStr, 'i') : null;
      } catch {
        theoremReg = numberReg = defReg = exReg = null;
      }
      const linesArr = lines.map((line, idx) => {
        const trimmed = line.trim();
        let color = '', fontWeight = 600;
        let matchType = '';
        if (theoremReg && theoremReg.test(trimmed)) {
          matchType = 'theorem';
        } else if (numberReg && numberReg.test(trimmed)) {
          matchType = 'number';
        } else if (defReg && defReg.test(trimmed)) {
          matchType = 'definition';
        } else if (exReg && exReg.test(trimmed)) {
          matchType = 'example';
        }
        // 
        if (theoremViewType === 'all' || theoremViewType === matchType) {
          if (matchType === 'theorem') {
            color = '#fef08a'; fontWeight = 700;
          } else if (matchType === 'number') {
            color = '#f3e8ff';
          } else if (matchType === 'definition') {
            color = '#bbf7d0';
          } else if (matchType === 'example') {
            color = '#bae6fd';
          }
        }
        return (
          <div
            key={idx}
            style={{
              background: color || 'transparent',
              color: color ? '#222' : undefined,
              fontWeight,
              padding: '0 4px',
              borderRadius: 4,
              fontSize: 15,
              lineHeight: '1.7',
              whiteSpace: 'pre-wrap',
              cursor: color ? 'pointer' : 'default',
            }}
            id={color ? `hl-md-theorem-${idx}` : undefined}
          >
            {line || '\u00A0'}
          </div>
        );
      });
      return { highlightLines: linesArr, titleIdxs: [], paraIdxs: [], allIdxs: [] };
    }
    // 
    let titleArr = [], paraArr = [], allArr = [];
    let titleReg = null, paraReg = null;
    try { if (titlePrefix) titleReg = useRegex ? new RegExp(titlePrefix) : null; } catch (e) { titleReg = null; }
    try { if (paraPrefix) paraReg = useRegex ? new RegExp(paraPrefix) : null; } catch (e) { paraReg = null; }
    // 
    let paraSegIdxs = [];
    // paraPrefix  \n  \n{2,} 
    const isBlankLineSplit = paraPrefix === '\n' || paraPrefix === '\n{2,}';
    if (isBlankLineSplit) {
      let inPara = false;
      for (let i = 0; i < lines.length; i++) {
        if (lines[i].trim() !== '' && (!inPara || i === 0)) {
          paraSegIdxs.push(i);
          inPara = true;
        }
        if (lines[i].trim() === '') {
          inPara = false;
        }
      }
    } else {
      for (let i = 0; i < lines.length; i++) {
        const trimmed = lines[i].trim();
        let isParaSep = false;
        if (paraPrefix) {
          if (useRegex && paraReg && paraReg.test(trimmed)) isParaSep = true;
          else if (!useRegex && trimmed.startsWith(paraPrefix)) isParaSep = true;
        }
        if (isParaSep || i === 0) {
          paraSegIdxs.push(i);
        }
      }
    }
    // 
    for (let i = 0; i < lines.length; i++) {
      const trimmed = lines[i].trim();
      let isTitle = false;
      if (titlePrefix) {
        if (useRegex && titleReg && titleReg.test(trimmed)) isTitle = true;
        else if (!useRegex && trimmed.startsWith(titlePrefix)) isTitle = true;
      }
      if (isTitle) titleArr.push(i);
    }
    // paraArr 
    paraArr = paraSegIdxs;
    // allArr 
    allArr = Array.from(new Set([...titleArr, ...paraArr])).sort((a, b) => a - b);
    // 
    let linesArr;
    if (isBlankLineSplit) {
      // div
      linesArr = [];
      for (let p = 0; p < paraSegIdxs.length; p++) {
        const start = paraSegIdxs[p];
        const end = (p + 1 < paraSegIdxs.length) ? paraSegIdxs[p + 1] : lines.length;
        const paraLines = lines.slice(start, end);
        if (paraLines.every(l => l.trim() === '')) continue; // 
        linesArr.push(
          <div
            key={start}
            style={{
              background: '#e0f2fe',
              borderRadius: 4,
              marginBottom: 8,
              padding: '2px 4px',
              transition: 'background 0.2s',
            }}
            id={`hl-md-para-${start}`}
            onClick={() => {
              if (jumpType === 'para') setCurrentIdx(p);
              else if (jumpType === 'all') setCurrentIdx(allArr.indexOf(start));
            }}
          >
            <span style={{
              color: '#0369a1',
              fontWeight: 700,
              borderLeft: '4px solid #38bdf8',
              paddingLeft: 6,
              marginRight: 4,
              fontSize: 15,
              lineHeight: '1.7',
              cursor: 'pointer',
              background: 'inherit',
            }}>{paraLines[0] || '\u00A0'}</span>
            {paraLines.length > 1 && (
              <div style={{
                color: '#0369a1',
                fontWeight: 400,
                fontSize: 15,
                lineHeight: '1.7',
                marginLeft: 14,
                whiteSpace: 'pre-wrap',
                background: 'inherit',
              }}>{paraLines.slice(1).join('\n')}
              </div>
            )}
          </div>
        );
      }
    } else {
      linesArr = lines.map((line, idx) => {
        const isTitle = titleArr.includes(idx);
        const isPara = paraArr.includes(idx);
        return (
          <div
            key={idx}
            style={{
              background: isTitle ? '#fef08a' : isPara ? '#bae6fd' : 'transparent',
              color: isTitle ? '#b45309' : isPara ? '#0369a1' : undefined,
              fontWeight: isTitle ? 700 : isPara ? 600 : 400,
              padding: '0 4px',
              borderRadius: 4,
              fontSize: 15,
              lineHeight: '1.7',
              whiteSpace: 'pre-wrap',
              cursor: (isTitle || isPara) ? 'pointer' : 'default',
            }}
            id={isTitle ? `hl-md-title-${idx}` : isPara ? `hl-md-para-${idx}` : undefined}
            onClick={() => {
              if (isTitle && jumpType === 'title') setCurrentIdx(titleArr.indexOf(idx));
              else if (isPara && jumpType === 'para') setCurrentIdx(paraArr.indexOf(idx));
              else if ((isTitle || isPara) && jumpType === 'all') setCurrentIdx(allArr.indexOf(idx));
            }}
          >
            {line || '\u00A0'}
          </div>
        );
      });
    }
    return { highlightLines: linesArr, titleIdxs: titleArr, paraIdxs: paraArr, allIdxs: allArr };
  }, [content, titlePrefix, paraPrefix, jumpType, useRegex, mode, theoremPatternStr, numberPatternStr, definitionsPatternStr, examplesPatternStr, theoremViewType]);

  // 
  const idxArr = jumpType === 'title' ? titleIdxs : jumpType === 'para' ? paraIdxs : allIdxs;

  // 
  React.useEffect(() => {
    if (idxArr.length && idxArr[currentIdx] !== undefined) {
      const el = document.getElementById(
        jumpType === 'title' ? `hl-md-title-${idxArr[currentIdx]}` :
        jumpType === 'para' ? `hl-md-para-${idxArr[currentIdx]}` :
        (titleIdxs.includes(idxArr[currentIdx]) ? `hl-md-title-${idxArr[currentIdx]}` : `hl-md-para-${idxArr[currentIdx]}`)
      );
      if (el && containerRef.current) {
        el.scrollIntoView({ behavior: 'smooth', block: 'center' });
      }
    }
  }, [currentIdx, idxArr, jumpType, titleIdxs, paraIdxs]);

  // 
  React.useEffect(() => {
    setCurrentIdx(0);
  }, [jumpType, titlePrefix, paraPrefix, content, useRegex]);

  // 
  const getTheoremIdxArr = React.useCallback(() => {
    // theoremViewType
    const lines = content ? content.split(/\r?\n/) : [];
    let theoremReg, numberReg, defReg, exReg;
    try {
      theoremReg = theoremPatternStr ? new RegExp(theoremPatternStr, 'i') : null;
      numberReg = numberPatternStr ? new RegExp(numberPatternStr, 'i') : null;
      defReg = definitionsPatternStr ? new RegExp(definitionsPatternStr, 'i') : null;
      exReg = examplesPatternStr ? new RegExp(examplesPatternStr, 'i') : null;
    } catch {
      theoremReg = numberReg = defReg = exReg = null;
    }
    let arr = [];
    for (let i = 0; i < lines.length; i++) {
      const trimmed = lines[i].trim();
      let matchType = '';
      if (theoremReg && theoremReg.test(trimmed)) {
        matchType = 'theorem';
      } else if (numberReg && numberReg.test(trimmed)) {
        matchType = 'number';
      } else if (defReg && defReg.test(trimmed)) {
        matchType = 'definition';
      } else if (exReg && exReg.test(trimmed)) {
        matchType = 'example';
      }
      if (theoremViewType === 'all' || theoremViewType === matchType) {
        arr.push(i);
      }
    }
    return arr;
  }, [content, theoremPatternStr, numberPatternStr, definitionsPatternStr, examplesPatternStr, theoremViewType]);

  // 
  React.useEffect(() => {
    if (mode === 'theorem') {
      const idxArr = getTheoremIdxArr();
      if (idxArr.length && idxArr[theoremCurrentIdx] !== undefined) {
        const el = document.getElementById(`hl-md-theorem-${idxArr[theoremCurrentIdx]}`);
        if (el && containerRef.current) {
          el.scrollIntoView({ behavior: 'smooth', block: 'center' });
        }
      }
    }
  }, [theoremCurrentIdx, getTheoremIdxArr, mode]);

  // /
  React.useEffect(() => {
    setTheoremCurrentIdx(0);
  }, [theoremViewType, content, theoremPatternStr, numberPatternStr, definitionsPatternStr, examplesPatternStr]);

  return (
    <div style={{border:'1.2px solid #b6c2d1',borderRadius:8,maxHeight:220,overflowY:'auto',background:'#f8fafc',padding:12,marginBottom:8,fontFamily:'inherit',fontSize:15,position:'relative'}} ref={containerRef}>
      {mode === 'theorem' && (
        <div style={{marginBottom:8,display:'flex',alignItems:'center',gap:8,position:'sticky',top:0,zIndex:2,background:'#f8fafc',paddingTop:2,paddingBottom:2}}>
          <span style={{fontSize:13,color:'#2563eb',fontWeight:600}}></span>
          <button onClick={()=>setTheoremViewType('all')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:theoremViewType==='all'?'#2563eb':'#fff',color:theoremViewType==='all'?'#fff':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          <button onClick={()=>setTheoremViewType('theorem')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:theoremViewType==='theorem'?'#fef08a':'#fff',color:theoremViewType==='theorem'?'#b45309':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          <button onClick={()=>setTheoremViewType('number')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:theoremViewType==='number'?'#f3e8ff':'#fff',color:theoremViewType==='number'?'#a21caf':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          <button onClick={()=>setTheoremViewType('definition')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:theoremViewType==='definition'?'#bbf7d0':'#fff',color:theoremViewType==='definition'?'#15803d':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          <button onClick={()=>setTheoremViewType('example')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:theoremViewType==='example'?'#bae6fd':'#fff',color:theoremViewType==='example'?'#0369a1':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          {/*  */}
          <span style={{color:'#888',fontSize:13,marginLeft:16}}></span>
          <button disabled={getTheoremIdxArr().length===0||theoremCurrentIdx<=0} onClick={()=>setTheoremCurrentIdx(i=>Math.max(i-1,0))} style={{padding:'2px 10px',borderRadius:6,border:'1px solid #b6c2d1',background:'#fff',color:'#2563eb',fontWeight:600,cursor:theoremCurrentIdx<=0||getTheoremIdxArr().length===0?'not-allowed':'pointer'}}></button>
          <span style={{color:'#888',fontSize:13}}>{getTheoremIdxArr().length?`${theoremCurrentIdx+1}/${getTheoremIdxArr().length}`:''}</span>
          <button disabled={getTheoremIdxArr().length===0||theoremCurrentIdx>=getTheoremIdxArr().length-1} onClick={()=>setTheoremCurrentIdx(i=>Math.min(i+1,getTheoremIdxArr().length-1))} style={{padding:'2px 10px',borderRadius:6,border:'1px solid #b6c2d1',background:'#fff',color:'#2563eb',fontWeight:600,cursor:theoremCurrentIdx>=getTheoremIdxArr().length-1||getTheoremIdxArr().length===0?'not-allowed':'pointer'}}></button>
        </div>
      )}
      {/* ... */}
      {mode !== 'theorem' && (
        <div style={{
          marginBottom:8,
          display:'flex',
          alignItems:'center',
          gap:8,
          position:'sticky',
          top:0,
          zIndex:2,
          background:'#f8fafc',
          paddingTop:2,
          paddingBottom:2,
        }}>
          <span style={{fontSize:13,color:'#2563eb',fontWeight:600}}></span>
          <button onClick={()=>setJumpType('all')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:jumpType==='all'?'#2563eb':'#fff',color:jumpType==='all'?'#fff':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          <button onClick={()=>setJumpType('title')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:jumpType==='title'?'#fef08a':'#fff',color:jumpType==='title'?'#b45309':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          <button onClick={()=>setJumpType('para')} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:jumpType==='para'?'#bae6fd':'#fff',color:jumpType==='para'?'#0369a1':'#2563eb',fontWeight:600,cursor:'pointer'}}></button>
          <button onClick={()=>setUseRegex(v=>!v)} style={{padding:'2px 8px',borderRadius:6,border:'1px solid #b6c2d1',background:useRegex?'#2563eb':'#fff',color:useRegex?'#fff':'#2563eb',fontWeight:600,cursor:'pointer'}} title="">{useRegex?':':':'}</button>
          <button disabled={currentIdx<=0 || idxArr.length===0} onClick={()=>setCurrentIdx(i=>Math.max(i-1,0))} style={{padding:'2px 10px',borderRadius:6,border:'1px solid #b6c2d1',background:'#fff',color:'#2563eb',fontWeight:600,cursor:currentIdx<=0||idxArr.length===0?'not-allowed':'pointer'}}></button>
          <span style={{color:'#888',fontSize:13}}>{idxArr.length?`${currentIdx+1}/${idxArr.length}`:''}</span>
          <button disabled={currentIdx>=idxArr.length-1 || idxArr.length===0} onClick={()=>setCurrentIdx(i=>Math.min(i+1,idxArr.length-1))} style={{padding:'2px 10px',borderRadius:6,border:'1px solid #b6c2d1',background:'#fff',color:'#2563eb',fontWeight:600,cursor:currentIdx>=idxArr.length-1||idxArr.length===0?'not-allowed':'pointer'}}></button>
        </div>
      )}
      {highlightLines}
    </div>
  );
}

export default MarkdownRawPreview;
