import React from 'react';

// 
function ParagraphSplitPreview({ content, paraPrefix, mode }) {
  //  split 
  const [useRegex] = React.useState(true);
  let blocks = [];
  if (!paraPrefix || paraPrefix === '\n' || paraPrefix === '\n{2,}') {
    // 
    blocks = content ? content.split(/\n{2,}/).filter(b => b.trim()) : [];
  } else {
    try {
      const fixParaPrefix = paraPrefix.replace(/\\n/g, '\\r?\\n');
      if (useRegex) {
        if (mode === 'book-section') {
          // 
          const rawBlocks = content ? content.split(new RegExp(fixParaPrefix, 'g')).filter(b => b.trim()) : [];
          blocks = rawBlocks.map(b => b.startsWith(fixParaPrefix) ? b : fixParaPrefix + b);
        } else {
          blocks = content ? content.split(new RegExp(fixParaPrefix, 'g')).filter(b => b.trim()) : [];
        }
      } else {
        if (mode === 'book-section') {
          const rawBlocks = content ? content.split(paraPrefix).filter(b => b.trim()) : [];
          blocks = rawBlocks.map(b => b.startsWith(paraPrefix) ? b : paraPrefix + b);
        } else {
          blocks = content ? content.split(paraPrefix).filter(b => b.trim()) : [];
        }
      }
    } catch {
      blocks = content ? [content] : [];
    }
  }
  return (
    <div style={{maxHeight:180,overflowY:'auto',border:'1px solid #e0e7ef',borderRadius:6,padding:4,background:'#fff'}}>
      {blocks.map((block, idx) => (
        <div
          key={idx}
          style={{
            background: '#f8fafc',
            borderRadius: 8,
            marginBottom: 12,
            padding: '10px 14px',
            fontSize: 15,
            lineHeight: '1.7',
            whiteSpace: 'pre-wrap',
            borderLeft: '5px solid #38bdf8',
            boxShadow: '0 2px 8px #bae6fd33',
            transition: 'box-shadow 0.2s, background 0.2s',
            cursor: 'pointer',
            position: 'relative',
          }}
          onMouseOver={e => e.currentTarget.style.background = '#e0f2fe'}
          onMouseOut={e => e.currentTarget.style.background = '#f8fafc'}
        >
          {block}
        </div>
      ))}
    </div>
  );
}

export default ParagraphSplitPreview;
