import React, { useState } from 'react';
import PromptManager from './PromptManager';
import { getGlobalApiKey } from './ApiConfigurator';

function JsonAnote({
  show,
  onClose,
  onStart,
  defaultToken = '',
  defaultApiUrl = 'https://api.siliconflow.cn/v1/chat/completions',
  defaultModel = 'Qwen/Qwen3-8B',
  defaultTemperature = 0.7,
  defaultTopP = 0.7,
  defaultTopK = 50,
  defaultFrequencyPenalty = 0.5,
  defaultMinP = 0.05,
  defaultMaxTokens = 4096,
  defaultThinkingBudget = 4096,
}) {
  const [token, setToken] = useState(defaultToken || getGlobalApiKey());
  const [apiUrl, setApiUrl] = useState(defaultApiUrl);
  const [model, setModel] = useState(defaultModel);
  const [temperature, setTemperature] = useState(defaultTemperature);
  const [topP, setTopP] = useState(defaultTopP);
  const [topK, setTopK] = useState(defaultTopK);
  const [frequencyPenalty, setFrequencyPenalty] = useState(defaultFrequencyPenalty);
  const [minP, setMinP] = useState(defaultMinP);
  const [maxTokens, setMaxTokens] = useState(defaultMaxTokens);
  const [thinkingBudget, setThinkingBudget] = useState(defaultThinkingBudget);
  const [minLength, setMinLength] = useState(30);

  // prompt 
  const promptTemplates = [
    {
      label: '///',
      value: `"- ""****"\n\n`
    },
    {
      label: '',
      value: `"- ""- "`
    },
    {
      label: '',
      value: `1.  2.  3.  4.  5. `
    },
    {
      label: '',
      value: ``
    },
    {
      label: '',
      value: `   `
    }
  ];
  const [promptIndex, setPromptIndex] = useState(0);
  const [prompt, setPrompt] = useState(promptTemplates[0].value);

  // API
  const apiOptions = [
    { label: 'SiliconFlow API', value: 'default', url: defaultApiUrl, model: defaultModel },
    { label: 'DeepSeek API', value: 'deepseek', url: 'https://api.deepseek.com/v1/chat/completions', model: 'deepseek-chat' },
  ];
  const [apiType, setApiType] = useState('default');

  // 
  const [showPromptManager, setShowPromptManager] = useState(false);
  const [customPrompts, setCustomPrompts] = useState(() => {
    try {
      const saved = localStorage.getItem('jsonanote_prompts');
      if (saved) return JSON.parse(saved);
    } catch {}
    return [];
  });
  const [editingPrompts, setEditingPrompts] = useState([]);
  const [editingChanged, setEditingChanged] = useState(false);
  // 
  const allPromptTemplates = [...promptTemplates, ...customPrompts];

  // 
  React.useEffect(() => {
    if (showPromptManager) {
      setEditingPrompts(JSON.parse(JSON.stringify(customPrompts)));
      setEditingChanged(false);
    }
  }, [showPromptManager]);

  // 
  const saveCustomPrompts = (prompts) => {
    setCustomPrompts(prompts);
    try { localStorage.setItem('jsonanote_prompts', JSON.stringify(prompts)); } catch {}
    setShowPromptManager(false);
  };



  // 
  React.useEffect(() => {
    const globalKey = getGlobalApiKey();
    if (globalKey && !token) setToken(globalKey);
  }, [show]);

  // APIAPI
  React.useEffect(() => {
    const selected = apiOptions.find(opt => opt.value === apiType);
    if (selected) {
      setApiUrl(selected.url);
      setModel(selected.model);
      // API
      if (apiType === 'deepseek') {
        const deepseekKey = localStorage.getItem('global_api_key_deepseek') || '';
        setToken(deepseekKey);
      } else {
        const siliconflowKey = localStorage.getItem('global_api_key_siliconflow') || '';
        setToken(siliconflowKey);
      }
    }
  }, [apiType]);

  const handleStart = () => {
    // 
    if (token) {
      try { localStorage.setItem('global_api_key', token); } catch {}
    }
    if (apiType === 'deepseek') {
      // DeepSeek  headers/payload
      const deepseekHeaders = {
        'Authorization': `Bearer ${token}`,
        'Content-Type': 'application/json',
      };
      const deepseekPayload = {
        model,
        messages: [
          { role: 'user', content: prompt },
        ],
        temperature,
      };
      onStart && onStart({
        apiType,
        apiUrl,
        token,
        model,
        temperature,
        prompt,
        minLength,
      });
    } else {
      onStart && onStart({
        token,
        apiUrl,
        model,
        temperature,
        topP,
        topK,
        frequencyPenalty,
        minP,
        maxTokens,
        thinkingBudget,
        minLength,
        prompt,
      });
    }
    onClose && onClose();
  };

  // Ctrl+D  handleStart
  React.useEffect(() => {
    if (!show) return;
    const handleKeyDown = (e) => {
      if ((e.ctrlKey || e.metaKey) && e.key === 'Tab') {
        e.preventDefault();
        handleStart();
      }
    };
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [show, token, apiUrl, model, temperature, topP, topK, frequencyPenalty, minP, maxTokens, thinkingBudget, minLength, prompt, apiType]);

  if (!show) return null;

  return (
    <div style={{ position: 'fixed', top: 0, left: 0, width: '100vw', height: '100vh', background: 'rgba(0,0,0,0.18)', zIndex: 2000, display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
      <div style={{ background: '#fff', borderRadius: 16, boxShadow: '0 4px 32px #2563eb22', padding: 36, minWidth: 420, maxWidth: 520, width: '100%', position: 'relative', display: 'flex', flexDirection: 'column', maxHeight: '90vh', overflow: 'auto' }}>
        <button style={{ position: 'absolute', top: 12, right: 18, background: 'none', border: 'none', color: '#2563eb', fontSize: 22, fontWeight: 700, cursor: 'pointer' }} onClick={onClose} title=""></button>
        <h3 style={{marginTop:0,marginBottom:18,fontWeight:700,fontSize:20,color:'#2563eb'}}>AI</h3>
        <div style={{marginBottom:18}}>
          <label style={{fontWeight:600,fontSize:15}}>API Token</label>
          <input type="password" value={token} onChange={e=>setToken(e.target.value)} style={{marginLeft:8,padding:'4px 12px',borderRadius:6,border:'1.2px solid #b6c2d1',width:260}} placeholder="API" />
        </div>
        <div style={{marginBottom:18}}>
          <label style={{fontWeight:600,fontSize:15}}>API</label>
          <input type="text" value={apiUrl} onChange={e=>setApiUrl(e.target.value)} style={{marginLeft:8,padding:'4px 12px',borderRadius:6,border:'1.2px solid #b6c2d1',width:260}} />
        </div>
        <div style={{marginBottom:18}}>
          <label style={{fontWeight:600,fontSize:15}}></label>
          <input type="text" value={model} onChange={e=>setModel(e.target.value)} style={{marginLeft:8,padding:'4px 12px',borderRadius:6,border:'1.2px solid #b6c2d1',width:180}} />
        </div>
        <div style={{marginBottom:18,display:'flex',gap:12}}>
          <div>
            <label style={{fontWeight:600,fontSize:15}}>temperature</label>
            <input type="number" step="0.01" min="0" max="2" value={temperature} onChange={e=>setTemperature(Number(e.target.value))} style={{marginLeft:8,padding:'4px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',width:60}} />
          </div>
          <div>
            <label style={{fontWeight:600,fontSize:15}}>top_p</label>
            <input type="number" step="0.01" min="0" max="1" value={topP} onChange={e=>setTopP(Number(e.target.value))} style={{marginLeft:8,padding:'4px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',width:60}} />
          </div>
          <div>
            <label style={{fontWeight:600,fontSize:15}}>top_k</label>
            <input type="number" step="1" min="1" max="100" value={topK} onChange={e=>setTopK(Number(e.target.value))} style={{marginLeft:8,padding:'4px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',width:60}} />
          </div>
        </div>
        <div style={{marginBottom:18,display:'flex',gap:12}}>
          <div>
            <label style={{fontWeight:600,fontSize:15}}>frequency_penalty</label>
            <input type="number" step="0.01" min="0" max="2" value={frequencyPenalty} onChange={e=>setFrequencyPenalty(Number(e.target.value))} style={{marginLeft:8,padding:'4px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',width:60}} />
          </div>
          <div>
            <label style={{fontWeight:600,fontSize:15}}>min_p</label>
            <input type="number" step="0.01" min="0" max="1" value={minP} onChange={e=>setMinP(Number(e.target.value))} style={{marginLeft:8,padding:'4px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',width:60}} />
          </div>
        </div>
        <div style={{marginBottom:18,display:'flex',gap:12}}>
          <div>
            <label style={{fontWeight:600,fontSize:15}}>max_tokens</label>
            <input type="number" step="1" min="1" max="8192" value={maxTokens} onChange={e=>setMaxTokens(Number(e.target.value))} style={{marginLeft:8,padding:'4px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',width:80}} />
          </div>
          <div>
            <label style={{fontWeight:600,fontSize:15}}>thinking_budget</label>
            <input type="number" step="1" min="1" max="8192" value={thinkingBudget} onChange={e=>setThinkingBudget(Number(e.target.value))} style={{marginLeft:8,padding:'4px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',width:80}} />
          </div>
        </div>
        <div style={{marginBottom:18}}>
          <label style={{fontWeight:600,fontSize:15}}></label>
          <input type="number" min="1" max="1000" value={minLength} onChange={e=>setMinLength(Number(e.target.value))} style={{marginLeft:8,padding:'4px 12px',borderRadius:6,border:'1.2px solid #b6c2d1',width:80}} />
          <span style={{color:'#888',marginLeft:8}}></span>
        </div>
        <div style={{marginBottom:12}}>
          <label style={{fontWeight:600,color:'#2563eb'}}>AIPrompt</label>
          <button onClick={()=>setShowPromptManager(true)} style={{marginLeft:8,padding:'2px 10px',borderRadius:6,border:'1.2px solid #2563eb',background:'#e0f2fe',color:'#2563eb',fontWeight:600,fontSize:14,cursor:'pointer'}}></button>
          <select value={promptIndex} onChange={e => {
            setPromptIndex(Number(e.target.value));
            setPrompt(allPromptTemplates[Number(e.target.value)]?.value || '');
          }} style={{marginLeft:8,padding:'2px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',fontSize:14}}>
            {allPromptTemplates.map((tpl, idx) => (
              <option value={idx} key={idx}>{tpl.label}</option>
            ))}
          </select>
          <textarea
            value={prompt}
            onChange={e=>setPrompt(e.target.value)}
            rows={5}
            style={{width:'100%',border:'1px solid #b6c2d1',borderRadius:6,padding:8,fontSize:14,marginTop:4,resize:'vertical'}}
          />
        </div>
        {showPromptManager && (
          <PromptManager
            editingPrompts={editingPrompts}
            setEditingPrompts={setEditingPrompts}
            editingChanged={editingChanged}
            setEditingChanged={setEditingChanged}
            saveCustomPrompts={saveCustomPrompts}
            setShowPromptManager={setShowPromptManager}
          />
        )}
        <div style={{marginBottom:18}}>
          <label style={{fontWeight:600,fontSize:15}}>API</label>
          <select value={apiType} onChange={e => setApiType(e.target.value)} style={{marginLeft:8,padding:'2px 8px',borderRadius:6,border:'1.2px solid #b6c2d1',fontSize:14}}>
            {apiOptions.map(opt => (
              <option value={opt.value} key={opt.value}>{opt.label}</option>
            ))}
          </select>
        </div>
        <button style={{ background: '#2563eb', color: '#fff', border: 'none', borderRadius: 8, padding: '8px 24px', fontWeight: 700, fontSize: 16, cursor: 'pointer', marginTop: 8, boxShadow: '0 2px 8px #2563eb22' }} onClick={handleStart}>
          
        </button>
      </div>
    </div>
  );
}

export default JsonAnote;
