import { useEffect } from 'react';

const SearchButton = ({ onOpenModal }) => {
  // Ctrl+P/Command+P
  useEffect(() => {
    const handleKeyDown = (e) => {
      if ((e.ctrlKey || e.metaKey) && e.key.toLowerCase() === 'p') {
        e.preventDefault(); // 
        onOpenModal(true); // 
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [onOpenModal]);

  return (
    <button
      onClick={onOpenModal}
      style={{
        padding: '6px 14px',
        fontSize: 14,
        fontWeight: 500,
        color: '#2563eb',
        background: '#f3f6fa',
        border: '1.5px solid #2563eb',
        borderRadius: 8,
        cursor: 'pointer',
        display: 'flex',
        alignItems: 'center',
        gap: 6,
        transition: 'all 0.2s',
        boxShadow: '0 1px 3px rgba(37, 99, 235, 0.1)',
      }}
      title=" (Ctrl+P)"
    >
      <svg width="16" height="16" fill="none" viewBox="0 0 16 16">
        <circle cx="7" cy="7" r="5" stroke="#2563eb" strokeWidth="1.5"/>
        <path d="M13 13L10.5 10.5" stroke="#2563eb" strokeWidth="1.5" strokeLinecap="round"/>
      </svg>
      <span></span>
    </button>
  );
};

export default SearchButton;